@import "./base/_vars.sass";
@import "./base/_mixins.sass";
.navigation {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    &__wrap {
        background: ($contacts - #010101);
        height: 70px;
        line-height: 70px; }
    &__center {
        display: inline-block;
        left: 50%;
        position: relative;
        transform: translateX(-50%); }
    &__menu {
        float: right; }
    &__item {
        float: left; }
    &__link {
        text-decoration: none;
        font-size: 14px;
        color: $colorText;
        font-weight: 300;
        letter-spacing: 1.4px;
        padding: 10px 13px;
        &:hover {
            color: $dark; }
        &_search {
            padding: 10px 13px; } }
    &__active,
    &__check {
        color: $dark;
        font-weight: 600;
        letter-spacing: 1.3px; }
    &__active {
        border-bottom: 3px solid $dark; } }
